<template>
	<div>
		<van-nav-bar class="hader" title="菜谱详情" left-text="返回" left-arrow @click-left="back()">
		  <a href="https://m.guanfan.top/#/app" slot="right" v-if="isiOS!=true&&isWeixin!=true">
		    <van-button type="primary" size="small">安装APP</van-button>
		  </a>
		  <a href="https://apps.apple.com/cn/app/id1487127641" slot="right" v-if="isiOS==true&&isWeixin!=true">
		    <van-button type="primary" size="small">安装APP</van-button>
		  </a>
		  <a href="https://mp.weixin.qq.com/mp/profile_ext?action=home&__biz=MzU0NTg5ODQzNA==&scene=124#wechat_redirect" slot="right" v-if="isWeixin==true">
		    <van-button type="primary" size="small">关注</van-button>
		  </a>
		</van-nav-bar>
		<div class="food-img mt35"
		     :style="{backgroundImage:'url('+osshost+ menuDetail.cpimg +detailpic+')'}"></div>
		<div class="cont mt10" style="margin-top: 0.5rem;">
		  <!--左边区域-->
		    <!--封面图-->
		    
		    <div class="menu_info mt10">
		      <div class="name text-cut-two">{{ menuDetail.title }}</div>
		      <div class="info clearfix">
		        
		        <div class="grayColor">
				  <span><van-icon class="icon-like2 grayColor ml10" name="eye" >{{menuDetail.visits}}</van-icon></span>
		        </div>
		      </div>
		    </div>
			<a href="https://m.guanfan.top/#/app" slot="right" v-if="isiOS!=true&&isWeixin!=true">
			  <van-button class="wb100 mt10 mb10" type="primary">保存菜谱做法到手机</van-button>
			</a>
			<a href="https://apps.apple.com/cn/app/id1487127641" slot="right" v-if="isiOS==true&&isWeixin!=true">
			  <van-button class="wb100 mt10 mb10" type="primary">保存菜谱做法到手机</van-button>
			</a>
			  <div v-if="isWeixin==true" class="gzh-guanzhu">
				  <img src="../assets/img/qr.jpg"/>
				  <span>长按识别二维码，关注管饭美食</span>
			  </div>
		    <!--用户信息-->
		    <div>
		      <ul class="dr_list">
		        <li class="clearfix mb15">
		          <div class="left headicon bg-img br50"
		               :style="{backgroundImage:'url('+osshost+ menuDetail.faceimg +userface+')'}">
		          </div>
		          <div class="info">
		            <h2 class="text-bold text-df text-16">{{menuDetail.nickname}}</h2>
					<span class="grayColor text-14">{{menuDetail.pushtime}}</span>
		          </div>
				  <i class="icon-like van-icon van-icon-like text-right right mt10" :class="menuDetail.iscollectioned==1?'text-red':''" @click="changeCollection()">{{ menuDetail.collections }}</i>
		        </li>
		      </ul>
		      <div class="text-df">{{menuDetail.story}}</div>
		    </div>
		    <!--  食材配料    -->
		    <div class="mt20">
		      <h2>食材配料</h2>
		      <div class="border">
		        <div class="clearfix text-df" v-for="(item,index) in stuff" :key="index">
		          <span class="left text-bold">{{item.name}}</span>
		          <span class="right">{{item.num}}</span>
		        </div>
		      </div>
		    </div>
		    <!--  制作步骤    -->
		    <div class="mt20">
		      <h2>制作步骤</h2>
		      <div v-for="(item,index) in menuDetail.step" :key="index">
		        <div class="mainColor height-30"><span class="text-df textTop">步骤</span><span
		          class="text-xsl">{{item.sort}}</span>
		        </div>
		        <div class="clearfix mt15">
		          <div v-if="item.stepImg!=''">
		            <div class="foodlist-img br5"
		                 :style="{backgroundImage:'url('+osshost+ item.stepImg +listpic+')'}"></div>
		          </div>
		          <div class="text-df">
		            {{item.description}}
		          </div>
		        </div>
		      </div>
		    </div>
		    <div class="mt45">
		      
		    </div>
		
		
		
		</div>
	</div>
</template>

<script>
	import qs from 'qs';
	import axios from 'axios';
	export default {
		name: "cookbookinfo",
		data() {
		  return {
		    num: 13,
		    sortList: '',
			stuff:'',
		    menuDetail: '',
		    osshost: 'https://guanfan.oss-cn-hangzhou.aliyuncs.com/',
			// osshost: 'http://guanfan.oss-cn-hangzhou.aliyuncs.com/',
		    listpic: '?x-oss-process=style/list-img',
		    userface: '?x-oss-process=style/user-img',
		    detailpic: '?x-oss-process=style/detail-img',
			isWeixin:'',
			isiOS:'',
			token:''
		  }
		},
		created() {
		  this.cpid = this.$route.query.id;
		  this.token = this.$store.state.user.token || '';
		  this.getDataInfo(this.cpid);
		  this.getwx();
		  this.appSource();
		},
		methods: {
			getwx(){
						var ua = window.navigator.userAgent.toLowerCase(); 
						  if (ua.match(/MicroMessenger/i) == 'micromessenger') {
						    this.isWeixin=true;
						  } else {
						   this.isWeixin=false;
						  } 
			},
			appSource() {
						const u = navigator.userAgent, app = navigator.appVersion;
						if (!!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)) {
							this.isiOS=true;
						} else {
							this.isiOS=false;
						}
					},
		  // getDataInfo(cpid) {
			 //  getMenuDetail(cpid).then(res=>{
				//   if(res.data.success){
				// 	  this.menuDetail = res.data.result;
				// 	  this.stuff = this.menuDetail.stuff.split('|');
				// 	  for (var j = 0; j < this.stuff.length; j++) {
				// 	    let array = [];
				// 	    array = this.stuff[j].split('#');
				// 	    this.stuff[j] = {
				// 	      name: array[0],
				// 	      num: array[1]
				// 	    }
				// 	  }
				//   }
			 //  }).catch(error=>{
				//   console.log(error)
			 //  })
		   
		  // },
		  getDataInfo(cpid) {
		  	let data = qs.stringify({
		  		cpid: cpid
		  	});
		  	axios({
		  		method: 'post',
		  		url: '/detail/recipe',
		  		headers: {
		  			'Content-Type': 'application/x-www-form-urlencoded',
					'token':this.token
		  		},
		  		data
		  	}).then(response => {
		  		if (response.data.success) {
		  			this.menuDetail = response.data.result;
		  			this.stuff = this.menuDetail.stuff.split('|');
		  			for (var j = 0; j < this.stuff.length; j++) {
		  			  let array = [];
		  			  array = this.stuff[j].split('#');
		  			  this.stuff[j] = {
		  			    name: array[0],
		  			    num: array[1]
		  			  }
		  			}
		  		}
		  	});
		  },
		  changeCollection(){
		  	if(this.token==""){
		  			this.$router.push({ path: '/pswLogin'});
		  	}else{
		  		if(this.menuDetail.iscollectioned==1){
					let data = qs.stringify({
						itemid:this.cpid,
						itemtype:2
					});
					axios({
						method: 'post',
						url: '/action/uncollection',
						headers: {
							'Content-Type': 'application/x-www-form-urlencoded',
							'token':this.token
						},
						data
					}).then(response => {
						if (response.data.success) {
							this.menuDetail.iscollectioned=0;
							this.menuDetail.collections--
						}
					});
		  			// setUncollection(this.cpid,2).then(res=>{
		  			// 	if(res.data.success){
		  			// 		this.menuDetail.iscollectioned=0;
		  			// 	    this.menuDetail.collections--
		  			// 	}
		  			// })
		  		}else{
					let data = qs.stringify({
						itemid:this.cpid,
						itemtype:2
					});
					axios({
						method: 'post',
						url: '/action/collection',
						headers: {
							'Content-Type': 'application/x-www-form-urlencoded',
							'token':this.token
						},
						data
					}).then(response => {
						if (response.data.success) {
							this.menuDetail.iscollectioned=1;
							this.menuDetail.collections++
						}
					});
		  			// setcollection(this.cpid,2).then(res=>{
		  			// 	if(res.data.success){
		  			// 		this.menuDetail.iscollectioned=1;
		  			// 	    this.menuDetail.collections++
		  			// 	}
		  			// })
		  		}
		  	}
		  },
		back(){
		        this.$router.go(-1);//返回上一层
		    },
		}
	}
</script>

<style>
	.van-icon-like:before{
		display: inline-block;
		position: relative;
	}
</style>
